import {Field, Form, Formik, status} from "formik";
import style from './../components/Profile/AvatarInfo/AvatarInfo.module.css';
import React from "react";
import * as Yup from 'yup';

const validate = Yup.object().shape({
    fullName: Yup.string()
        .min(2, 'Too Short!')
        .max(30, 'Too Long!'),
    aboutMe: Yup.string()
        .min(2, 'Too Short!')
        .max(300, 'Too Long!'),
    lookingForAJobDescription: Yup.string()
        .min(2, 'Too Short!')
        .max(100, 'Too Long!'),
    contacts:Yup.string()
        .email('Invalid url'),
});

const InfoForm = (props) => (

    <div>
        <Formik
            initialValues={{ fullName: '', lookingForAJob: '', lookingForAJobDescription: '', aboutMe: '', contacts: '',
            }}
            validationSchema={validate}
            onSubmit={(values, {setStatus}) => {
                props.onSubmit(values, setStatus);
            }}
        >
            {({ errors, touched, status}) => (
                <Form >
                    <div>
                        <b>Name</b>:
                        < Field name="fullName" placeholder="Name"/>
                            {touched.fullName && errors.fullName && <div>{errors.fullName}</div>}
                    </div>
                    <div>
                        <b>Looking for a job</b>:
                        < Field name="lookingForAJob" type="checkbox"/>
                    </div>
                    <div>
                        <b>Professional skills</b>:
                        < Field name="lookingForAJobDescription" placeholder="Professional skills"/>
                        {touched.lookingForAJobDescription && errors.lookingForAJobDescription && <div>{errors.lookingForAJobDescription}</div>}
                    </div>
                    <div>
                        <b>About me</b>:
                        < Field name="aboutMe" placeholder="About me"/>
                        {touched.aboutMe && errors.aboutMe && <div>{errors.aboutMe}</div>}
                    </div>
                    <div>
                        <b>Contacts</b>: {Object.keys(props.profile.contacts).map(key => {
                        return <div key={key} className={style.contacts}>
                                <b> {key}: < Field type="url" name={"contacts." + key} placeholder={key} /> </b>
                        </div>
                    })}
                    </div>
                    <div>{status}</div>
                    < button type="submit">Save</button>
                </Form>
            )}
        </Formik>
    </div>);

export default InfoForm;