import React, {useState} from 'react';
import style from './AvatarInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import UserPhoto from "./../../../assets/images/UserPhoto.jpg"
import InfoForm from "../../../forms/InfoForm";

const AvatarInfo = (props) => {

    let [editMode, setEditMode] = useState(false)

    if (!props.profile) {
        return <Preloader/>
    }

    const ProfileData = (props) => {
        return <div className={style.info}>
            <div>
                <div className={style.info_left}>
                    <b>Name</b>: {props.profile.fullName}
                </div>
                <div>
                    <b>Looking for a job</b>: {props.profile.lookingForAJob ? "yes" : "no"}
                </div>
                {props.profile.lookingForAJob &&
                <div>
                    <b>My professional skills</b>: {props.profile.lookingForAJobDescription}
                </div>
                }
                <b>Contacts</b>:{Object.keys(props.profile.contacts).map(key => {
                return <Contact contactTitle={key} contactValue={props.profile.contacts[key]}/>
            })}
                {props.isOwner && <div>
                    <button onClick={props.toEditMode}>edit</button>
                </div>}
            </div>
            <div className={style.about_me}>
                <b>About me</b>: {props.profile.aboutMe}
            </div>
        </div>
    };

    const mainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0]);
        }
    }

    const Contact = ({contactTitle, contactValue}) => {
        return <div className={style.contact}>
            <b>{contactTitle}</b>:{contactValue}
        </div>
    }
    const onSubmit = (values, setStatus) => {
        props.saveInfo(values, setStatus);
            setEditMode(false);
    }

    return <div className={style.AvatarInfo}>
        <div className={style.avatar_status}>
            <div>
            <img src={props.profile.photos.large || UserPhoto}/>
                <div className={style.upload_img}>
            {props.isOwner && <input type={"file"} title=" " onChange={mainPhotoSelected} />}
                </div>
            </div>
            <div className={style.status}><ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
            </div>
        </div>
        <div >
            {editMode
                ? <InfoForm profile={props.profile} onSubmit={onSubmit}/>
                : <ProfileData toEditMode={() => {
                    setEditMode(true)
                }} profile={props.profile} isOwner={props.isOwner}/>}
        </div>
    </div>
};


export default AvatarInfo;