import React from 'react';
import style from './Post.module.css';

const PostsItem = (props) => {
    return <div className={style.post}>{props.post}</div>
}

const Post = (props) => {
    let PostElements =
        props.posts.map(pr => <PostsItem post={pr.post} key={pr.id} id={pr.id}/>);

    return <div className={style.post_elements}>
        {PostElements}
    </div>
}
export default Post;